body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
}

.div_button {
  width: 100%;
  margin: 50px auto;
  text-align: center;
}

button {
  display: inline-block;
  margin: 20px;
  height: 60px;
  width: 150px;
  color: #fff;
  background-color: rgb(44, 173, 212);
  border: 1px solid black;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  font: inherit;
  font-size: 22px;
  font-weight: 400;
}

button:hover {
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
  background-color: red;
}
